@import url(https://fonts.googleapis.com/css2?family=Domine&family=Noto+Sans+JP&family=Nanum+Gothic&display=swap);
:root {
  --light-cyan: #E0FBFC;
  --pale-cerulean: #98C1D9;
  --bdazzled-blue: #3D5A80;
  --gunmetal: #293241;
  --burnt-sienna: #EE6C4D;
}

* {
  box-sizing: border-box;
  font-family: 'Nanum Gothic';
}

html, body {
  height: 100%;
}

body {
  background-color: #FFFFFF;
}

#root {
  height: 100%;
}

.cursor-pointer {
  cursor: pointer;
}
section {
    position: relative;
    height: 100%;
}

main {
    height: 100%;
}

.btn {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
}
nav {
    height: 100%;
    overflow: hidden;
}

.file-cabinet {
    height: 100%;

    position: relative;

    list-style: none;
    padding: 0;
    margin: 0;
}

.file-cabinet-menu, 
.file-cabinet-page-header,
.file-item {
    position: absolute;
    width: 100%;
}

.file-cabinet-menu {
    top: 0;

    height: 8%;
    display: grid;
    grid-template-columns: 1fr 6fr 1fr;

    z-index: 5;

    color: white;
    transition: color 600ms;
}

.file-cabinet-menu h1 {
    font-family: 'Domine';
}

.file-cabinet-menu.menu-pressed {
    color: var(--gunmetal)
}

.file-cabinet-page-header {
    position: absolute;
    top: 8%;

    height: 8%;

    display: -webkit-flex;

    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
}

.file-cabinet-page-header h3, .file-item h3 { 
    font-weight: bold;
    font-family: 'Noto Sans JP';
}

.file-cabinet-page-header h3 {
    margin: 0;
}

.menu-button-div {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    padding-left: 10px;
}
.file-cabinet-menu span {
    font-size: 2rem; 
}
.file-cabinet-menu h1 {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;

    margin: 0;

    font-size: 2rem;
}

.file-item {
    border-radius: 50px 50px 0 0;

    text-align: center;
    color: white;

    padding-top: 8vh;
    
    bottom: 0;

    transition: top 600ms, border-radius 600ms;
}

.file-item h3 {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;

    margin: 0;

    height: 8vh;
}

.bp-menu-item {
    background-color: var(--bdazzled-blue);
    top: 16%;
}
.pp-menu-item {
    background-color: var(--pale-cerulean);
    top: 37%;
}
.ab-menu-item {
    background-color: var(--burnt-sienna);
    top: 58%;
}
.ct-menu-item {
    background-color: var(--gunmetal);
    top: 79%;
}

.option-down {
    top: 100%;
}

.option-selected {
    top: 0%;
    border-radius: 0;
}

.component-container {
    opacity: 0;
    transition: opacity 1s;

    overflow-y: auto;

    height: 92%;

    display: -webkit-flex;

    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: space-between;
            justify-content: space-between;
}

.component-visible {
    opacity: 1;
}

@media only screen and (min-width: 325px) {
    .file-cabinet-menu h1 {
        font-size: 2.5rem;
    }
}

@media only screen and (min-width: 450px) {
    .menu-button-div {
        padding-left: 5vw;
    }
}
.projects-main-container {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;

    -webkit-justify-content: space-around;

            justify-content: space-around;
    -webkit-align-items: center;
            align-items: center;
}

.project-sub-container {    
    -webkit-flex-basis: 100%;    
            flex-basis: 100%;

    display: -webkit-flex;

    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;

    padding: 2rem 2rem
}
.project-sub-container h2 {
    margin: 0;
    font-size: 1.25rem;
}

.project-sub-container .project {
    width: 300px;
    height: 300px;

    display: -webkit-flex;

    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;

    margin-bottom: 1rem;

    background-color: white;
    color: var(--gunmetal);
    box-shadow: 0 4px 8px 0 var(--gunmetal), 0 6px 20px 0 var(--gunmetal);

    cursor: pointer;
}

.project-sub-container .project:active {
    box-shadow: none
}

.project-img {
    width: 300px
}

@media only screen and (min-width: 600px) {
    .project-sub-container {    
        -webkit-flex-basis: 50%;    
                flex-basis: 50%;
    }
}

@media only screen and (min-width: 900px) {
    .project-sub-container {    
        -webkit-flex-basis: 33%;    
                flex-basis: 33%;
    }
}
.modalBackdrop {
	background: rgba(0, 0, 0, .65);
	bottom: 0;
	left: 0;
	overflow: auto;
	position: fixed;
	right: 0;
	top: 0;
	z-index: 10;

    display: -webkit-flex;

    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: flex-start;
            align-items: flex-start;
    padding: 0 5%;
}

.modalContainer {
	background: var(--gunmetal);
	border-radius: 5px;
	max-width: 100%;
	margin: 50px auto;
	padding: 15px;
	width: 960px;

    position: relative;
}

.modalTitle {
	font-size: 2.5rem;
	margin-bottom: 25px;
    font-weight: bold;
    font-family: 'Noto Sans JP';
}

.modalContainer img {
    width: 75%;
    margin: 10px 0
}

button.close {
    position: absolute;
    top: 10px;
    right: 10px;

    color: white;
}
.project-btn-group,
a.project-btn {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: flex-start;
            justify-content: flex-start;
    -webkit-align-items: center;
            align-items: center;
}
.project-btn-group {
    -webkit-flex-direction: column;
            flex-direction: column;
}
a.project-btn {
    width: 80%;
    margin: 5px
}
a.project-btn > *:first-child {
    margin-right: 1rem;
}

.project-description {
    margin: 1.5rem 0;
    text-align: left;
}

.technologies-icons {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;

    margin: .5rem 0
}

.technologies-icons div {
    margin: 0 .5rem;

    display: -webkit-flex;

    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
}

.technologies-icons h6 {
    font-size: .75rem;
    margin: .2rem 0 0 0;
}

@media only screen and (min-width: 450px) {
    a.project-btn {
        width: 50%;
    }
    .project-description {
        margin: 1.5rem 1rem;
    }
}

@media only screen and (min-width: 600px) {
    a.project-btn {
        width: 260px;
    }
    .modalContainer img {
        width: 50%;
    }
    .project-btn-group {
        -webkit-flex-direction: row;
                flex-direction: row;
        -webkit-justify-content: center;
                justify-content: center;
    }
    .project-description {
        width: 350px;
        margin: 1.5rem auto;
    }
}

@media only screen and (min-width: 900px) {
    .project-description {
        width: 490px;
        margin: 1.5rem auto;
    }
}
.footer {
    margin-top: 20px;
    margin-bottom: 30px;
}

.footer-container {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
}

.footer-tile {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;

    margin: 0 10px;
}
.footer-tile a {
    text-decoration: none;

    display: -webkit-flex;

    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
}

.contact-text {
    margin-top: 1rem;
}

.footer-tile .material-icons,
.react-icon-footer,
.contact-text {
    color: var(--gunmetal);
}

.footer-tile .material-icons {
    font-size: 2rem;
}
.react-icon-footer {
    height: 2rem;
    width: 2rem;
}
.contact-container {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;

    padding: 20px
}
.contact-tile {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;

    -webkit-flex-basis: 50%;

            flex-basis: 50%;

    padding: 10px;

    
}
.contact-tile a {
    text-decoration: none;

    background-color: white;

    height: 150px;
    width: 100%;

    display: -webkit-flex;

    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;

    padding: 10px;
}

.contact-text {
    margin-top: 1rem;
}

.contact-tile .material-icons,
.react-icon,
.contact-text {
    color: var(--gunmetal);
}

.contact-tile .material-icons {
    font-size: 4rem;
}
.react-icon {
    height: 4rem;
    width: 4rem;
}
.about-me-container {
    margin: 2rem;   
}

.profile-pic {
    width: 100%;

    border-radius: 25px;
}

.about-me-text {
    margin-top: 1rem;
    text-align: left;
}

.resume-link a {
    text-decoration: underline;
    color: white;
    font-weight: bold;
}

@media only screen and (min-width: 450px) {
    .profile-pic {
        width: 350px;
    }
    .about-me-container {
        margin: 2rem 4rem;   
    }
}

@media only screen and (min-width: 600px) {
    .about-me-container {
        margin: 2rem 20%;   
    }
}

@media only screen and (min-width: 900px) {
    .about-me-container {
        margin: 2rem auto;

        display: -webkit-flex;

        display: flex;
        -webkit-justify-content: center;
                justify-content: center;
        -webkit-align-items:flex-start;
                align-items:flex-start; 
    }

    .about-me-text {
        width: 500px;
        margin-left: 2rem;
    }
}
a.resume-button {
    z-index: 8;

    height: 40px;
    width: 40px;

    background-color: var(--light-cyan);

    border-radius: 50%;

    position: absolute;
    top: 2%;
    right: 10px;

    display: -webkit-flex;

    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;

    box-shadow: 2px 2px 3px lightgray;

    transition: top 1s;
}

a.resume-button:active,
a.resume-button:visited,
a.resume-button:hover {
    text-decoration: none;
}
a.resume-button:active {
    box-shadow: none
}

a.resume-button.bottom {
    top: 88%;
}

a.resume-button span {
    color: var(--gunmetal);

    display: -webkit-flex;

    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;

    font-size: 1.5rem;
}

p.resume-label {
    color: var(--gunmetal);
    margin: 0;
    padding: 0;

    font-size: .5rem;

}

@media only screen and (min-width: 325px) {
    a.resume-button {
        height: 50px;
        width: 50px;
    }
    a.resume-button span {
        font-size: 2rem;
    }
    p.resume-label {
        font-size: .65rem;
        font-weight: bold;
    }
}

@media only screen and (min-width: 375px) {
    a.resume-button {
        height: 60px;
        width: 60px;
    }
    a.resume-button span {
        font-size: 2.5rem;
    }
}

@media only screen and (min-width: 450px) {
    a.resume-button {
        right: 5%;
    }
}
